<template>
	<div class="modal">
	<div class="content">{{ text }}</div>
	<div class="button" @click="$emit('close')">OK</div>

	</div>
</template>

<script>
export default {
	props: ['text']
}
</script>

<style lang="scss" scoped>
	.modal {
		position: fixed;
		// width: 70%;
		// width: 90vw;
		// height: 90vh;
		// width: 100%;
		max-width: 90vw;
		background: #f1f1f1;
		border: 1px solid black;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		display: inline-flex;
		flex-direction: column;
	}
	.button {
		border-top: 1px solid black;
		// justify-content: flex-end;
		padding-right: 2rem;
	}
	.content {
		display: flex;
		flex-grow: 1;
		max-width: 100vh;
		align-items: center;
		justify-content: center;
		padding: 2rem;
		// white-space: nowrap;
		
	}
</style>



