<template>
	<section>
		<Modal v-if="modalText !== ''" :text="modalText" v-on:close="modalText = ''"/>
		<div class="button" @click="open">öffnen</div>
		<div class="button" @click="logout">abmelden</div>
	</section>
</template>

<script>
const firebase = require('firebase/app')
require('firebase/auth')
import Modal from './Modal'
export default {
	props: ['token'],
	components: {
		Modal
	},
	data() {return{
		modalText: ""
	}},
	methods: {
		logout() {
			firebase.auth().signOut().then(() => this.$emit('navigation', 'Login'))
		},
		open() {
			firebase.auth().currentUser.getIdToken(true)
			.then(idToken => {
				fetch('/api/open', {
					method: "POST",
					body: JSON.stringify({token: idToken}),
					headers: {"Content-Type": "application/json"}
				})
				.then(res => {
					if(res.status !== 200)
						this.modalText = res.status + ': ' + res.statusText
					return res.json()
				})
				.then(json => {
					if(json.error)
						this.modalText = json.error
				})
			})
			

		}
	}
}
</script>

<style lang="scss" scoped>
	section {
		// height: 100%;
		background: #fff;
		
		display: flex;
		flex-direction: column;
		> * {
			
			flex-grow: 1;
		}
		> *:not(:last-child) {
			border-bottom: 1px solid #000;
		}
	}

	@media (orientation: landscape) {
		section {
			flex-direction: row;
			.button {
				width: 50%;
			}
			> .button:not(:last-child) {
				border-right: 1px solid #000;
				border-bottom: none;
			}
		}
	}
</style>



